@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;600;700;900&display=swap);
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}

.card img {
  width: 100%;
}

.card-container {
  padding: 6px 16px;
  background-color: #fff;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.card-title-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

.container {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.grid-container {
  display: grid;
  grid-gap: 2rem;
  gap: 2rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
}

select {
  display: inline;
  margin-left: 0.5rem;
  padding: 0.5rem 2rem;
  margin-bottom: 24px;
}

img {
  border-radius: 8;
}

p {
  text-align: center;
}

.content-container {
  display: 'flex';
  -webkit-flex-direction: 'column';
          flex-direction: 'column';
  -webkit-justify-content: 'center';
          justify-content: 'center';
  -webkit-align-items: 'center';
          align-items: 'center';
}

button {
  text-decoration: none;
  display: inline-block;
  padding: 16px 32px;
  border: none;
  border-radius: 8px;
  margin-bottom: 16px;
  background-color: #f1f1f1;
  color: black;
}

button:hover {
  background-color: #ddd;
  color: black;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.header {
  left: 0;
  top: 0;
  width: 100%;
  height: 12rem;
  background-color: #0c6eef;
  padding: 0 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  box-sizing: border-box;
}

.header-title {
  color: #ffffff;
}

.content {
  margin: 2rem 2.5rem;
}

body {
  font-family: 'Source Sans Pro', sans-serif;
  margin: 0;
}

